body {
  color: #4a4a4a;
  background-color: #fff0f5;
  margin: 0;
  padding: 20px;
  font-family: Segoe UI, sans-serif;
}

.container {
  max-width: 900px;
  margin: 0 auto;
}

h2 {
  color: #d63384;
  margin-bottom: 15px;
  font-size: 1.8rem;
}

.form-section, .posts-section {
  background-color: #ffe4ec;
  border-radius: 12px;
  margin-bottom: 30px;
  padding: 25px;
  box-shadow: 0 4px 12px #d633841a;
}

form {
  flex-direction: column;
  gap: 12px;
  display: flex;
}

input, textarea {
  background-color: #fff;
  border: 1px solid #f5c2d7;
  border-radius: 8px;
  padding: 10px;
  font-size: 1rem;
  transition: border-color .3s;
}

input:focus, textarea:focus {
  border-color: #d63384;
  outline: none;
}

textarea {
  resize: vertical;
  min-height: 100px;
}

.add {
  color: #fff;
  cursor: pointer;
  background-color: #d63384;
  border: none;
  border-radius: 8px;
  padding: 10px;
  font-size: 1rem;
  transition: background-color .3s;
}

.add:hover {
  background-color: #b02a6f;
}

#postList {
  margin: 0;
  padding: 0;
  list-style: none;
}

#postList li {
  background-color: #fff;
  border-left: 5px solid #d63384;
  border-radius: 10px;
  margin-bottom: 15px;
  padding: 15px;
  box-shadow: 0 2px 6px #d6338414;
}
